import React, { Component, Fragment } from "react";
import { Table } from "react-bootstrap";
import TdHead from "./TdHead";
import "./CommonTable.css";

class CommonTable extends Component {
  constructor(props) {
    // props: data (object), mode (str), sortable (bool)
    // theadCols (array), createTbody (function),
    // defaultSortKey (str)
    super(props);
    this.state = {
      sortAsc: -1,
      sortKey: this.props.defaultSortKey
    };
    this.onTheadClick = this.onTheadClick.bind(this);
  }

  onTheadClick(key) {
    this.setState(prev => ({
      sortAsc: key === prev.sortKey ? -prev.sortAsc : -1,
      sortKey: key
    }));
  }

  render() {
    const { sortAsc, sortKey } = this.state;
    const { data, mode } = this.props;
    const sortable = mode === "year";
    const sortFunction = (a, b) => sortAsc * (a[sortKey] - b[sortKey]);

    if (sortable) data.sort(sortFunction);
    return (
      <Table className="data-table" responsive hover>
        <thead>
          <tr>
            {createThead(
              this.props.theadCols,
              mode,
              sortable,
              sortKey,
              this.onTheadClick
            )}
          </tr>
        </thead>
        <tbody>
          {data.map((d, i) => (
            <tr key={i}>{this.props.createTbody(d, i, mode)}</tr>
          ))}
        </tbody>
      </Table>
    );
  }
}

function createThead(theadCols, mode, sortable, sortKey, onTheadClick) {
  return (
    <Fragment>
      <TdHead type="thead" mode={mode} />
      {theadCols.map(
        (d, i) =>
          sortable && d.sortable ? (
            <th
              key={i}
              className={d.key === sortKey ? "sortable sorted" : "sortable"}
              onClick={() => {
                if (sortable && d.sortable) {
                  onTheadClick(d.key);
                }
              }}
            >
              <span>{d.label}</span>
            </th>
          ) : (
            <th key={i} className={d.emphasized ? "em" : ""}>
              {d.label}
            </th>
          )
      )}
    </Fragment>
  );
}

export default CommonTable;



