import React from 'react';
import PaneTemplate from './PaneTemplate';
import Article from '../Article';

import RevTable from './RevTable';

import md from './revenue.md';

const RevPane = ({ eventKey, data, mode }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <RevTable data={data} mode={mode} />
    <article>
      <Article md={md} />
    </article>
  </PaneTemplate>
);

export default RevPane;



