import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const emphasized = true;
const PLTheadCols = [
  { label: '', key: '', sortable: true},
  { label: '', key: '', sortable: true},
  { label: '', key: '', sortable: true, emphasized },
  { label: '', key: '' },
  { label: '', key: '' },
  { label: '', key: '', sortable: true, emphasized },
  { label: '', key: '' },
  { label: '', key: '' },
  { label: '', key: '', emphasized },
  { label: '', key: '' },
  { label: '', key: '', sortable: true, emphasized }
];

const PLTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey=""
    theadCols={PLTheadCols}
    createTbody={createPLTbody}
  />
);

function createPLTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      {PLTheadCols.map(obj => (
        <td key={obj.label} className={obj.emphasized ? 'em' : ''}>
          {d[obj.key]}
        </td>
      ))}
    </Fragment>
  );
}

export default PLTable;



