import React from 'react';
import PaneTemplate from './PaneTemplate';
import Article from '../Article';
import AttdTable from './AttdTable';

import md from './Attd.md';

const AttdPane = ({ eventKey, data, mode }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <AttdTable data={data} mode={mode} />
    <article>
      <Article md={md} />
    </article>
  </PaneTemplate>
);

export default AttdPane;


