import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const AttdTheadCols = [
  { label: "", key: "", sortable: true, emphasized: true },
  { label: "", key: "", sortable: true },
  { label: "", key: "", sortable: true},
  { label: "", key: "", sortable: true},
  { label: "", key: "", sortable: true }
];

const AttdTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey=""
    theadCols={AttdTheadCols}
    createTbody={createAttdTbody}
  />
);

function createAttdTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td>{d['']}</td>
      <td>{Math.round(d[''])}</td>
      <td>{d['']}</td>
      <td>{d[''] || d['']}</td>
      <td>{Math.round(d[''])}</td>
    </Fragment>
  );
}

export default AttdTable;



