import React, { Component } from "react";
import { Grid, Row, Col, Tab } from "react-bootstrap";
import Sidebar from "react-sidebar";

import ClubSelector from "./components/ClubSelector";
import AppHeader from "./components/AppHeader";
import {
  PaneController,
  MobilePaneController
} from "./components/PaneController";
import PLPane from "./components/panes/PLPane";
import BSPane from "./components/panes/BSPane";
import RevPane from "./components/panes/RevPane";
import ExpPane from "./components/panes/ExpPane";
import AttdPane from "./components/panes/AttdPane";
import Attribution from "./components/Attribution";
import Loader from "./components/Loader";

const mql = window.matchMedia(`(min-width: 800px)`);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      item: null,
      fetching: false,
      tab: "pl",
      sidebarDocked: mql.matches,
      sidebarOpen: false
    };
    this.fetchItem = this.fetchItem.bind(this);
    this.selectItem = this.selectItem.bind(this);
    this.handleTab = this.handleTab.bind(this);
    this.parseHash = this.parseHash.bind(this);
    this.setHash = this.setHash.bind(this);

    this.mediaQueryChanged = this.mediaQueryChanged.bind(this);
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  cache = {};

  componentWillMount() {
    mql.addListener(this.mediaQueryChanged);
    this.parseHash();
  }

  componentWillUnmount() {
    mql.removeListener(this.mediaQueryChanged);
  }

  fetchItem(id) {
    this.setState({
      fetching: true,
      sidebarOpen: false
    });
    return import(`./dataset/${id}.json`).then(json => {
      const item = {
        data: json,
        mode: id < 2000 ? "club" : "year"
      };
      this.setState({ item, sidebarOpen: false, fetching: false }, () => {
        this.cache[id] = json;
      });
    });
  }

  selectItem(id) {
    const mode = id < 2000 ? "club" : "year";
    if (this.cache[id]) {
      this.setState(
        {
          item: {
            data: this.cache[id],
            mode
          }
        },
        () => {
          this.setHash();
        }
      );
    } else {
      this.fetchItem(id).then(() => {
        this.setHash();
      });
    }
  }

  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
  }

  mediaQueryChanged() {
    this.setState({ sidebarDocked: mql.matches, sidebarOpen: false });
  }

  handleTab(key) {
    this.setState(
      {
        tab: key
      },
      () => {
        this.setHash();
      }
    );
  }

  parseHash() {
    // # club = 1 & tab = pl

    if (window.location.hash === "") {
      this.fetchItem(1).then(() => {
        this.setHash();
      });
      return;
    }
    const hash = window.location.hash;
    const parts = hash.slice(1).split("&");
    const status = {};

    parts.forEach((part, i) => {
      const [key, val] = part.split("=");
      if (key === "item") {
        status[key] = parseInt(val, 10);
      } else if (key === "tab") {
        status[key] = val;
      }
    });

    if (status.item < 0 || status.item > 2017) {
      this.fetchItem(1).then(() => {
        this.setHash();
      });
      return;
    }
    if (status.item > 54 && status.item < 2006) {
      this.fetchItem(1).then(() => {
        this.setHash();
      });
      return;
    }

    this.fetchItem(status.item).then(() => {
      this.setState(
        prev => ({
          tab: status.tab || prev.tab
        }),
        () => {
          this.setHash();
        }
      );
    });
  }

  setHash() {
    const { item, tab } = this.state;
    const hash = `#item=${item.data.id}&tab=${tab}`;

    window.history.pushState(this.state, "", hash);
  }

  render() {
    const { item, tab, fetching } = this.state;
    if (item === null) return <Loader />;
    const data = Object.values(item.data.data);
    const mode = item.mode;
    return (
      <Sidebar
        sidebar={
          <ClubSelector
            selectedId={item.data.id}
            onItemClick={this.selectItem}
          />
        }
        open={this.state.sidebarOpen}
        docked={this.state.sidebarDocked}
        onSetOpen={this.onSetSidebarOpen}
        styles={{
          sidebar: {
            backgroundColor: 'white',
            overflowY: "auto",
            WebkitOverflowScrolling: "touch"
          }
        }}
      >
        <Grid fluid>
          <AppHeader
            title={item.data.fullName}
            onSetSidebarOpen={this.onSetSidebarOpen}
            fetching={fetching}
          />
          <Tab.Container id="tabs" activeKey={tab} onSelect={this.handleTab}>
            <Row>
              <PaneController />
              <MobilePaneController
                activeKey={tab}
                handleTab={this.handleTab}
              />
              <Col xs={12} sm={12}>
                <Tab.Content animation>
                  <PLPane eventKey="pl" data={data} mode={mode} />
                  <BSPane eventKey="bs" data={data} mode={mode} />
                  <RevPane eventKey="revenue" data={data} mode={mode} />
                  <ExpPane eventKey="expense" data={data} mode={mode} />
                  <AttdPane eventKey="attd" data={data} mode={mode} />
                </Tab.Content>
              </Col>
            </Row>
          </Tab.Container>
          <Row>
            <Col xs={12} sm={12}>
              <aside>
                <Attribution />
              </aside>
            </Col>
          </Row>
        </Grid>
      </Sidebar>
    );
  }
}

export default App;



