import React from 'react';
import ReactLoading from 'react-loading';
import { Row, Col, Glyphicon } from 'react-bootstrap';

const FetchStatus = () => (
  <ReactLoading className="loader" type="spokes" width={42} height={42} />
);

const Title = ({ title }) => (
  <div className="title">
    <span>
      {title}
    </span>
  </div>
)

const AppHeader = ({ title, fetching, onSetSidebarOpen }) => (
  <Row>
    <Col
      xs={12}
      smHidden
      mdHidden
      lgHidden
      className="club-selector-header"
    >
      <button
        className="header-button"
        onClick={() => onSetSidebarOpen(true)}
      >
        <Glyphicon glyph="menu-hamburger" />
      </button>
      {fetching ? (
        <FetchStatus />
      ) : (
        <Title title={title} />
      )}
    </Col>
    <Col xsHidden sm={12} className="club-selector-header">
      {fetching ? (
        <FetchStatus />
      ) : (
        <Title title={title} />
      )}
    </Col>
  </Row>
);

export default AppHeader;



