import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const sortable = true;
const emphasized = true;
const BSTheadCols = [
  { label: "", key: "", sortable, emphasized },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "", sortable, emphasized },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "", sortable, emphasized },
  { label: "", key: "", sortable },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "", sortable }
];

const BSTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey=""
    theadCols={BSTheadCols}
    createTbody={createBSTbody}
  />
);

function createBSTbody(d, i, mode) {
  return (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      {BSTheadCols.map(obj => (
        <td key={obj.label} className={obj.emphasized ? "em" : ""}>
          {d[obj.key]}
        </td>
      ))}{/*
      <td>
        {d[""] && d[""]
          ? d3format(".0%")(d[""] / d[""])
          : ""}
      </td>
      <td>{d3format(".0%")(d[""] / d[""])}</td>*/}
    </Fragment>
  );
}

export default BSTable;



