import React from 'react';
import {
  Col,
  Nav,
  NavItem,
  FormGroup,
  ControlLabel,
  FormControl
} from 'react-bootstrap';

import './PaneController.css';

const ControlItems = [
  { eventKey: 'pl', label: '' },
  { eventKey: 'bs', label: '' },
  { eventKey: 'revenue', label: '' },
  { eventKey: 'expense', label: '' },
  { eventKey: 'attd', label: '' }
];

const PaneController = () => (
  <Col sm={12} xsHidden>
    <Nav bsStyle="tabs" justified className="pane-controller">
      {ControlItems.map((d, i) => (
        <NavItem key={d.eventKey} eventKey={d.eventKey}>
          {d.label}
        </NavItem>
      ))}
    </Nav>
  </Col>
);

const MobilePaneController = ({ activeKey, handleTab }) => (
  <Col xs={12} smHidden mdHidden lgHidden>
    <FormGroup
      controlId="formControlsSelect"
      className="pane-controller-mobile"
    >
      <ControlLabel></ControlLabel>
      <FormControl componentClass="select" value={activeKey} onChange={e => {
        handleTab(e.target.value);
      }}>
        {ControlItems.map((d, i) => (
          <option key={d.eventKey} value={d.eventKey}>
            {d.label}
          </option>
        ))}
      </FormControl>
    </FormGroup>
  </Col>
);

export { PaneController, MobilePaneController };



