import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const RevTheadCols = [
  { label: "", key: "", sortable: true, emphasized: true },
  { label: "", key: "", sortable: true },
  { label: "", key: "", sortable: true },
  { label: "", key: "J", sortable: true },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "" }
];

const RevTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey=""
    theadCols={RevTheadCols}
    createTbody={createRevTbody}
  />
);

function createRevTbody(d, i, mode) {
  const BasicRevs = () => (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d[""]}</td>
      <td>{d[""]}</td>
      <td>{d[""]}</td>
      <td>{d["J"]}</td>
    </Fragment>
  );

  return d[""] < 2011 ? (
    <Fragment>
      <BasicRevs />
      <td colSpan={3}>{d[""]}</td>
    </Fragment>
  ) : d[""] < 2016 ? (
    <Fragment>
      <BasicRevs />
      <td>{d[""]}</td>
      <td colSpan={2}>{d[""]}</td>
    </Fragment>
  ) : (
    <Fragment>
      <BasicRevs />
      <td>{d[""]}</td>
      <td>{d[""]}</td>
      <td>{d[""]}</td>
    </Fragment>
  );
}

export default RevTable;



