import React, { Fragment } from "react";

const TdHead = ({ d, i, type, mode }) =>
  type === "thead" ? (
    <Fragment>
      {mode === 'year' ? <th>i</th> : null}
      <th className="th-label">{mode === "year" ? "" : ""}</th>
      <th></th>
      <th></th>
    </Fragment>
  ) : (
    <Fragment>
      {mode === 'year' ? <th>{i + 1}</th> : null}
      <th className="th-label">
        {mode === "year" ? d[""] : d[""]}
      </th>
      <th>{d[""]}</th>
      <th>{d[""]}</th>
    </Fragment>
  );

export default TdHead;



