import React, { Fragment } from "react";
import CommonTable from "../ComTable";
import TdHead from "../TdHead";

const ExpTheadCols = [
  { label: "", key: "", sortable: true, emphasized: true },
  { label: "", key: "", sortable: true },
  { label: "", key: ""},
  { label: "", key: ""},
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "" },
  { label: "", key: "" }
];

const ExpTable = ({ data, mode }) => (
  <CommonTable
    data={data}
    mode={mode}
    sortable={mode === "year"}
    defaultSortKey=""
    theadCols={ExpTheadCols}
    createTbody={createExpTbody}
  />
);

function createExpTbody(d, i, mode) {

  return d[''] < 2011 ? (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={6}>{d['']}</td>
      <td colSpan={1}>{d['']}</td>
    </Fragment>
  ) : d[''] < 2016 ? (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={3}>{d['']}</td>
    </Fragment>
  ) : (
    <Fragment>
      <TdHead d={d} i={i} mode={mode} />
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={2}>{d['']}</td>
    </Fragment>
  );
}

export default ExpTable;



