import React from 'react';
import clubIds from '../clubIds';

import './ClubSelector.css';

const ClubSelector = ({ selectedId, onItemClick }) => (
  <div className="club-selector">
    <div className="club-selector-header">
      <div className="title">Menu</div>
    </div>
    <div>
      <h4>J1</h4>
      <ul>
        {clubIds['j1'].map((d, i) => (
          <a key={i} onClick={() => onItemClick(d.id)}>
            <li className={d.id === selectedId ? 'active' : ''}>{d.name}</li>
          </a>
        ))}
      </ul>
    </div>
    <hr />
    <div>
      <h4>J2</h4>
      <ul>
        {clubIds['j2'].map((d, i) => (
          <a key={i} onClick={() => onItemClick(d.id)}>
            <li className={d.id === selectedId ? 'active' : ''}>{d.name}</li>
          </a>
        ))}
      </ul>
    </div>
    <hr />
    <div>
      <h4>J3</h4>
      <ul>
        {clubIds['j3'].map((d, i) => (
          <a key={i} onClick={() => onItemClick(d.id)}>
            <li className={d.id === selectedId ? 'active' : ''}>{d.name}</li>
          </a>
        ))}
      </ul>
    </div>
    <div>
      <h4></h4>
      <ul>
        {Array.from({length: 12}, (d, i) => i + 2006).map((d, i) => (
          <a key={i} onClick={() => onItemClick(d)}>
            <li className={selectedId === d ? 'active' : ''}>{d}</li>
          </a>
        ))}
      </ul>
    </div>
  </div>
);

export default ClubSelector;



